import { FORM_EDIT } from '@components/hook-form/validation/formEdit'
import { UNIT_MEASURE } from '@constants/common'
import Card from '../../layout'
import style from './style.module.css'

const { METRE } = UNIT_MEASURE

const {
    HOU_FLOOR,
    HOU_FLOOR_SQUARE,
    HOU_BED,
    HOU_TOILET,
    HOU_COMPLETE,
    HOU_QUALITY } = FORM_EDIT

const HouseInfo = ({ f, control }) => {
    const leftItems = [
        { key: HOU_FLOOR, component: Card.Input },
        { key: HOU_FLOOR_SQUARE, component: Card.Input, suffix: METRE },
        { key: HOU_BED, component: Card.Input },
        { key: HOU_TOILET, component: Card.Input },
    ]

    const rightItems = [
        { key: HOU_COMPLETE, component: Card.Select, option: [] },
        { key: HOU_QUALITY, component: Card.Select, option: [] },
    ]

    return (
        <>
            <div>
                {leftItems.map(({ component, key, ...rest }) => (
                    component({
                        key,
                        label: f({ id: `edit_${key}` }),
                        className: style.formRow,
                        style: {marginBottom:'7px'},
                        name: key,
                        control,
                        ...rest
                    })
                ))}
            </div>
            <div>
                {rightItems.map(({ component, key, ...rest }) => (
                    component({
                        key,
                        label: f({ id: `edit_${key}` }),
                        placeholder: f({ id: key + "_holder" }),
                        style: {marginBottom:'7px'},
                        className: style.formRow,
                        name: key,
                        control,
                        ...rest
                    })
                ))}
            </div>
        </>
    )
}

export default HouseInfo