import { Upload } from "antd"
import { UploadService } from "@services/upload"
import { getBase64 } from "@utils"
import { MEDIA, ONLY_IMAGE, ONLY_VIDEO } from "@constants/request"

export const UPLOAD_TYPE = {
    VIDEO: "video",
    IMAGE: "image",
    MEDIA: "media"
}

const UploadCore = ({
    children,
    onFinish,
    type,
    ...props
}) => {

    const accept = {
        [UPLOAD_TYPE.VIDEO]: ONLY_VIDEO,
        [UPLOAD_TYPE.IMAGE]: ONLY_IMAGE
    }[type]

    const onBeforeUpload = async(file) => {
        const converBase64 = await getBase64(file)
        file.url = converBase64
        if (type === UPLOAD_TYPE.VIDEO) {
            const fileData = await UploadService.upVideo(converBase64)
            file.src = fileData?.data
        } else {
            const fileData = await UploadService.upImage(converBase64)
            file.src = fileData?.data
        }
        return file
    }

    const onChange = async ({ file, fileList }) => {
        if (file.status === 'error'){            
            onFinish(file)
        }
    }

    return (
        <Upload.Dragger
            name="upload"
            multiple={false}
            showUploadList={false}
            beforeUpload={onBeforeUpload}
            onChange={onChange}
            action={null}
            accept={accept || MEDIA}
            {...props}
        >
            {children}
        </Upload.Dragger>
    )
}

export default UploadCore