import { FORM_EDIT } from '@components/hook-form/validation/formEdit'
import { UNIT_MEASURE } from '@constants/common'
import Card from '../../layout'
import style from './style.module.css'

const { METRE, SQUARE_METRE } = UNIT_MEASURE

const { LAND_WIDTH,
    LAND_LONG,
    REAL_SQUARE,
    DEFAULT_SQUARE,
    CONTRUCT_SQUARE,
    ROAD_SQUARE,
    SHAPE,
    DIRECTION } = FORM_EDIT

const RealEstateInfo = ({ f, control }) => {
    const listItems = [
        { key: LAND_WIDTH, component: Card.Input, suffix: METRE },
        { key: LAND_LONG, component: Card.Input, suffix: METRE },
        { key: REAL_SQUARE, component: Card.Input, suffix: SQUARE_METRE },
        { key: DEFAULT_SQUARE, component: Card.Input, suffix: SQUARE_METRE },
        { key: CONTRUCT_SQUARE, component: Card.Input, suffix: SQUARE_METRE },
        { key: ROAD_SQUARE, component: Card.Input, suffix: SQUARE_METRE },
        { key: SHAPE, component: Card.Select, option: [], placeholder: f({id: SHAPE +'_holder'})},
        { key: DIRECTION, component: Card.Select, option: [], placeholder: f({id: DIRECTION + '_holder'})},
    ]

    return (

        listItems.map(({ component, key, ...rest }) => (
            component({
                key,
                label: f({ id: `edit_${key}` }),
                className: style.formRow,
                name: key,
                control,
                ...rest
            })
        ))
    )



}

export default RealEstateInfo