import { Controller } from "react-hook-form"
import { ReactComponent as Plus } from "@assets/icons/Plus.svg"
import { ReactComponent as Trash } from "@assets/icons/Trash.svg"
import { ReactComponent as Play } from "@assets/icons/Play.svg"
import { ReactComponent as Attached } from "@assets/icons/Attached.svg"
import UploadCore from "@components/upload"
import style from './style.module.css'
import { Button, Input, notification } from "antd"
import { memo, useState } from "react"
import { checkLinkVideo } from "@utils"
import ReactPlayer from "react-player"
import Card from "."

const MAX_FILE = 3

const BlockHolder = memo(({ src, onRemove }) =>
    src ?
        <div className={`${style.blockHolder} position-relative overflow-hidden`} >
            <ReactPlayer controls={false} playing={false} height={"100%"} width="100%" url={src} />
            <div className={style.mask}>
                <Button icon={<Play style={{ marginLeft: '3px' }} />} shape="circle" className={style.actionIcon} />
            </div>
            <Button onClick={onRemove} shape="circle" className={style.actionRemove} icon={<Trash />} />
        </div>
        : <div className={`${style.blockHolder}`} />)

const UploadVideo = ({ name, control, title, f }) => {

    const [stateInput, setStateInput] = useState({
        value: '',
        error: false
    })

    const renderPreview = (files, onChange) => {
        const handleRemove = (index) => {
            const arr = files
            arr?.splice(index, 1)
            onChange(arr)
        }
        return new Array(MAX_FILE).fill(null).map((_, index) => <BlockHolder key={index} onRemove={() => handleRemove(index)} src={files?.[index]} />)
    }

    const handleUpload = (file, value, onChange) => {
        if (value?.length > MAX_FILE) return
        onChange([...(value ? value : []), file])
    }

    const onCheckLink = (file, value, onChange) => {
        const isValid = checkLinkVideo(stateInput.value)
        if (!isValid) {
            setStateInput(pre => ({ ...pre, error: true }))
            notification.error({ message: f({id: 'linkInvalid'}) })
        } else {
            handleUpload(file, value, onChange)

        }
    }

    return (
        <Controller name={name} control={control} render={
            ({ field: { name, value, onChange } }) => (
                <Card.Form extend={f({ id: 'quantity' }, { x: value?.length })} title={title}>
                    <div className={style.formUploadImage}>
                        <div className={style.blockHolder}>
                            <UploadCore
                                name={name}
                                type={'video'}
                                maxCount={5}
                                onFinish={(file) => handleUpload(file?.url, value, onChange)}
                            >
                                <div className="d-flex flex-column align-items-center justify-content-center">
                                    <Button shape="circle" className={style.actionIcon} icon={<Plus />} />
                                    {f({id: 'uploadVideo'})}
                                </div>
                            </UploadCore>
                        </div>

                        <div className={style.linkVideo} style={{ gridColumn: 'span 2' }}>
                            <Button onClick={() => onCheckLink(stateInput.value, value, onChange)} shape="circle" disabled={!stateInput.value} className={style.actionIcon} icon={<Attached />} />
                            <span>{f({id: 'enterLink'})}</span>
                            <Input value={stateInput.value} onChange={({ target }) => setStateInput({ value: target.value, error: false })} className="w-75" />
                        </div>

                        {renderPreview(value, onChange)}

                    </div>
                </Card.Form>
            )
        } />
    )
}

export default UploadVideo