import { Avatar, Button, Card as CardAnt } from 'antd'
import { memo } from 'react'
import { ReactComponent as Trash } from "@assets/icons/Trash.svg"
import Card from '../../layout'
import { ReactComponent as Plus } from "@assets/icons/Plus.svg"
import style from './style.module.css'
import { useFieldArray } from 'react-hook-form'
import { FORM_EDIT, FORM_CONTACT_EDIT } from '@components/hook-form/validation/formEdit'
import { ROLE_TEXT } from '@constants/common'
import { ROLE_ID } from '@constants/common'
import { useIntl } from 'react-intl'

const { LAST_NAME, BIRTHDAY, PHONE, EMAIL } = FORM_CONTACT_EDIT

const CardContact = memo(({ avatar, roleId, remove, control, index, f }) => {
    const nameCard = `${FORM_EDIT.AGENT}[${index}]`
    const fields = [
        { name: LAST_NAME, component: Card.Input },
        { name: BIRTHDAY, component: Card.Input },
        { name: PHONE, component: Card.Input },
        { name: EMAIL, component: Card.Input },
    ]
    return (
        <CardAnt className='bg-transparent' bordered={false}>
            <div style={{ gap: '15px', flexWrap: 'nowrap' }} className='row justify-content-between align-items-center'>
                <Avatar size={60} src={avatar} shape="square" />
                <div className='flex-grow-1'>
                    <span className='h3'>{ROLE_TEXT[roleId]}</span>
                </div>
                <div>
                    <Button onClick={remove} icon={<Trash />} shape="circle" />
                </div>
            </div>
            <div style={{ rowGap: '15px' }} className='d-flex flex-column mt-2'>
                {
                    fields.map(({ component, name }) => (
                        component({
                            key: name,
                            control,
                            name: `${nameCard}.${name}`,
                            label: f({ id: name }),
                            placeholder: f({ id: name + '_holder' }),
                            className: `${style.formRow} ${style.inputBorderBottom}`
                        })
                    ))
                }
            </div>
        </CardAnt>
    )
})

const CardAddContact = ({ roleId, append }) => {
    const handleAppend = () => {
        const initialSchema = { roleId }
        Object.values(FORM_CONTACT_EDIT).map(v => initialSchema[v] = "")
        append(initialSchema)
    }

    return <Card className='d-flex justify-content-between align-items-center' bordered={false}>
        <Avatar className='mr-1' size={50} src={null} shape="square" />
        <div className='flex-grow-1'>
            <span className='h3'>{ROLE_TEXT[roleId]}</span>
        </div>
        <div>
            <Button onClick={handleAppend} icon={<Plus />} shape="circle" />
        </div>
    </Card>
}

const ContactInfo = ({ control }) => {
    const { formatMessage: f } = useIntl()
    const { fields, append, remove } = useFieldArray({
        control,
        name: FORM_EDIT.AGENT
    })

    const contactById = ({ roleId, ...rest }) =>
    ({
        [ROLE_ID.ADMIN]: <CardContact roleId={roleId} {...rest} />,
        [ROLE_ID.CHECKER]: <CardContact roleId={roleId} {...rest} />,
        [ROLE_ID.DATA_ADMIN]: <CardContact roleId={roleId} {...rest} />,
        [ROLE_ID.INPUTTER]: <CardContact roleId={roleId} {...rest} />,
        [ROLE_ID.SALE]: <CardContact roleId={roleId} {...rest} />,
    }[roleId]);

    return <div className={style.contactInfo}>

        {fields?.map((card, index) =>
            contactById({
                key: card.id,
                roleId: card?.roleId,
                remove: () => remove(index),
                control,
                f,
                index
            })
        )}

        <CardAddContact append={append} roleId={ROLE_ID.SALE} />
        <CardAddContact append={append} roleId={ROLE_ID.INPUTTER} />
    </div>
}

export default ContactInfo