import { Form, Select } from "antd"
import { Controller } from "react-hook-form"

const SelectForm = ({ name, label, control, option, onChange : change, ...selectProps }) => {
    const handleSelect = (changeForm, value) => {
        if(typeof change === 'function') change(value)
        changeForm(value)
    }
    return <Controller
        name={name}
        control={control}
        render={
            ({ field: { value, onChange }, formState: { errors } }) =>
                <Form.Item
                    label={label}
                    className="m-0"
                    validateStatus={errors?.[name] && "error"}
                    help={errors?.[name] && errors?.[name]?.message}
                >                
                    <Select value={value} onChange={(value)=> handleSelect(onChange, value)} options={option} {...selectProps} />
                </Form.Item>
            }        
    />
    }

export default SelectForm