import { Col, Row } from "antd"
import { useIntl } from "react-intl"
import { useFormContext } from "react-hook-form"
import { FORM_EDIT } from "@components/hook-form/validation/formEdit"
import { TEXT_AREA } from "@components/hook-form/components/InputForm"
import Card from "../../layout"
import Address from "./address"
import RealEstateInfo from "./realEstateInfo"
import style from './style.module.css'
import HouseInfo from "./houseInfo"
import JuridicalInfo from "./juridicalInfo"
import UploadImage from "../../layout/UploadImage"
import UploadVideo from "../../layout/UploadVideo"
import ContactInfo from "./contactInfo"
import NoteStick from "./noteStick"


const { UTILITY, ADVANTAGE, IMG_CERTIFICATE, IMG_BLUEPRINT, IMG_HOUSE, VID_HOUSE, IMG_360, DIS_ADVANTAGE, NOTE } = FORM_EDIT

const TabResInfo = () => {
    const { formatMessage: f } = useIntl()
    const { control, getValues } = useFormContext()

    return (
        <Row gutter={[25, 15]} >
            <NoteStick content={getValues(NOTE)}/>
            <Col span={17}>
                <Card.Form title={f({ id: 'address' })}>
                    <Address f={f} />
                </Card.Form>

                <Row className={style.editBody} gutter={15}>
                    <Col md={12} sm={24}>
                        <Card.Form classNameBody={style.twoCols} title={f({ id: 'realEstateInfo' })}>
                            <RealEstateInfo f={f} control={control} />
                        </Card.Form>
                    </Col>
                    <Col md={12} sm={24}>
                        <Card.Form classNameBody={style.twoCols} title={f({ id: 'houseInfo' })}>
                            <HouseInfo f={f} control={control} />
                        </Card.Form>
                    </Col>
                    <Col md={12} sm={24}>
                        <Card.Form classNameBody={style.twoCols} title={f({ id: 'juridicalInfo' })}>
                            <JuridicalInfo f={f} control={control} />
                        </Card.Form>
                    </Col>
                    <Col md={12} sm={24}>
                        <Card.Form title={f({ id: UTILITY })}>
                            <Card.Checkbox name={UTILITY} control={control} option={
                                getValues(UTILITY)?.map(opt => ({ label: opt?.realEstateUtilitiesName, value: opt?.realEstateUtilitiesId }))
                            } />
                        </Card.Form>
                    </Col>
                    <Col md={12} sm={24}>
                        <Card.Form title={f({ id: ADVANTAGE })}>
                            <Card.Checkbox name={ADVANTAGE} control={control} option={
                                getValues(ADVANTAGE).map(opt => ({ label: opt?.advantageName, value: opt?.advantageId }))
                            } />
                        </Card.Form>
                    </Col>
                    <Col md={12} sm={24}>
                        <Card.Form title={f({ id: DIS_ADVANTAGE })}>
                            <Card.Checkbox name={DIS_ADVANTAGE} control={control} option={
                                getValues(DIS_ADVANTAGE).map(opt => ({ label: opt?.advantageName, value: opt?.advantageId }))
                            } />
                        </Card.Form>
                    </Col>
                    <Col span={24}>
                        <Card.Form title={f({ id: NOTE })}>
                            <Card.Input bordered={false} className={style.noteInput} placeholder={f({ id: NOTE + "_holder" })} type={TEXT_AREA} name={NOTE} control={control} />
                        </Card.Form>
                    </Col>
                </Row>

                <Row className={style.editBodyImage} gutter={[25, 25]}>
                    <Col md={12} sm={24}>
                        <UploadImage f={f} title={f({ id: IMG_CERTIFICATE })} name={IMG_CERTIFICATE} control={control} />
                    </Col>
                    <Col md={12} sm={24}>
                        <UploadImage f={f} title={f({ id: IMG_BLUEPRINT })} name={IMG_BLUEPRINT} control={control} />
                    </Col>
                    <Col md={12} sm={24}>
                        <UploadImage f={f} title={f({ id: IMG_HOUSE })} name={IMG_HOUSE} control={control} />
                    </Col>
                    <Col md={12} sm={24}>
                        <UploadImage f={f} title={f({ id: IMG_360 })} name={IMG_360} control={control} />
                    </Col>
                    <Col md={12} sm={24}>
                        <UploadVideo f={f} title={f({ id: VID_HOUSE })} name={VID_HOUSE} control={control} />
                    </Col>
                </Row>
            </Col>

            <Col span={7}>
                <ContactInfo control={control} />
            </Col>
        </Row>
    )
}

export default TabResInfo