import { Checkbox, Form } from "antd"
import { Controller } from "react-hook-form"

const CheckBoxForm = ({ name, control, label, option, children, ...props }) => {
    const Check = option.length ? Checkbox.Group : Checkbox

    return <Controller
        name={name}
        control={control}
        render={
            ({ field: { value, onChange }, formState: { errors } }) =>
                <Form.Item
                    label={label}
                    className="m-0"
                    validateStatus={errors?.[name] && "error"}
                    help={errors?.[name] && errors?.[name]?.message}
                >
                    <Check
                        options={option}
                        value={value}
                        onChange={onChange}
                        {...props}
                    >{children}</Check>
                </Form.Item>
        }
    />
}

export default CheckBoxForm