import { ENDPOINTS } from "@constants/endpoints";
import requestService from "./request";

export class UploadService {
  static async upVideo(vidData, vidFormat = "mp4" ) {
    const body = {
      videoData: vidData,
      videoFormat: vidFormat,
    };
    const res = await requestService.send({
      method: "post",
      path: ENDPOINTS.RES_UPLOAD_VIDEO,
      data: body,
    });
    return res;
  }
  static async upImage(imgData, imgFormat = "png") {
    const body = {
      imageData: imgData,
      imageFormat: imgFormat,
    };
    const res = await requestService.send({
      method: "post",
      path: ENDPOINTS.RES_UPLOAD_IMAGE,
      data: body
    })
    return res
  }
}