import { Tabs } from "antd";
import { useIntl } from "react-intl";
import { ReactComponent as FileDocument } from '@assets/icons/FileDocument.svg'
import { ReactComponent as Customer } from '@assets/icons/Customer.svg'
import { ReactComponent as Recorvery } from '@assets/icons/Recorvery.svg'
import { ReactComponent as Chat } from '@assets/icons/Chat.svg'
import TabResInfo from "./tab-contents/resInfo";
import TabCustomerKey from "./tab-contents/customerKey";
import TabHistoryTransaction from "./tab-contents/historyTransaction";
import TabActivity from "./tab-contents/activity";
import style from './style.module.css'

const DEFAULT_TAB_ACTIVE = 0

const EditBody = () => {
    const { formatMessage: f } = useIntl()

    const tabList = [
        {
            icon: <FileDocument />,
            name: f({id: "generalInformation"}),
            content: <TabResInfo />,
        },
        {
            icon: <Customer />,
            name: f({id: "potentialCustomer"}),
            content: <TabCustomerKey />,
        },
        {
            icon: <Recorvery />,
            name: f({id: "history"}),
            content: <TabHistoryTransaction />,
        },
        {
            icon: <Chat />,
            name: f({id: "activities"}),
            content: <TabActivity />,
        },
    ];

    return (
        <Tabs size="large" className={style.tabsBar} defaultActiveKey={DEFAULT_TAB_ACTIVE}>
            {
                tabList.map((tab, key) => (
                    <Tabs.TabPane tab={<span style={{gap:'5px'}} className="d-flex align-items-center">{tab.icon} {tab.name}</span>} key={key}>
                        {tab.content}
                    </Tabs.TabPane>
                ))
            }
        </Tabs>
    )
}

export default EditBody