import { Form, Input } from "antd"
import { Controller } from "react-hook-form"

export const TEXT_AREA = "textArea"

const InputForm = ({ name, label, control, type, ...props })=>{
    const TextInput = type === TEXT_AREA ? Input.TextArea : Input
    return <Controller
        name={name}
        control={control}
        render={
            ({ field: { value, onChange }, formState: { errors } }) =>
                <Form.Item 
                    label={label} 
                    className="m-0"
                    validateStatus={errors?.[name] && "error"}
                    help={errors?.[name] && errors?.[name]?.message}
                >
                    <TextInput
                        value={value}
                        onChange={onChange}
                        {...props}
                    />
                </Form.Item>
        }
    />
}



export default InputForm