import { FORM_EDIT } from '@components/hook-form/validation/formEdit'
import { useFormContext } from 'react-hook-form'
import useAreaData from '@services/getAreaData'
import Card from '../../layout'
import { useEffect } from 'react'
import { AddressFilter } from '../../../../../@core/components/filters/address'

const { STREET, PROVINCE, DISTRICT, WARD } = FORM_EDIT

const Address = ({ f }) => {
    const { control, getValues } = useFormContext()

    const { address, cities, dists, blocks, onChangeAddress, setAddress } = useAreaData();

    useEffect(() => {
        setAddress({
            // street: (getValues(STREET)).trim(),
            city: getValues(PROVINCE),
            dist: getValues(DISTRICT),
            block: getValues(WARD),
        });
    }, [])
    // const colSchema = [PROVINCE, DISTRICT, WARD]
    // const optionArea = {
    //     [PROVINCE]: cities?.map(opt => ({ label: opt?.areaDataName, value: opt?.areaDataId })),
    //     [DISTRICT]: dists?.map(opt => ({ label: opt?.areaDataName, value: opt?.areaDataId })),
    //     [WARD]: blocks?.map(opt => ({ label: opt?.areaDataName, value: opt?.areaDataId }))
    // }
    return (
        <div style={{paddingLeft: '1px'}} className='row'>
            <AddressFilter
              onChangeHandle={onChangeAddress}
              address={address}
              cities={cities}
              dists={dists}
              blocks={blocks}
            //   breakpointResponse={1}
            />
            {/* <Card.Input placeholder={f({ id: STREET + '_holder' })} style={{ borderRadius: '5px' }} className="col" name={STREET} control={control} />
            {
                colSchema.map(col => (
                    <Card.Select
                        key={col}
                        className="col"
                        placeholder={f({ id: col + '_holder' })}
                        name={col}
                        onChange={onChangeAddress}
                        control={control}
                        option={optionArea[col]}
                    />
                ))
            } */}
        </div>
    )
}

export default Address