import { FORM_EDIT } from '@components/hook-form/validation/formEdit'
import Card from '../../layout'
import style from './style.module.css'

const { 
    JUR_NAME,
    JUR_AGENCY,
    JUR_STATUS,
    JUR_CERTIFICATE,
    JUR_PAPER,
    JUR_ZONING} = FORM_EDIT

const JuridicalInfo = ({ f, control }) => {
    const listItems = [
        { key: JUR_NAME, component: Card.Select, option: [], placeholder: f({id: JUR_NAME + '_holder'}) },
        { key: JUR_STATUS, component: Card.Select, option: [], placeholder: f({id: JUR_STATUS + '_holder'}) },
        { key: JUR_PAPER, component: Card.Input },
        { key: JUR_ZONING, component: Card.Select, option: [], placeholder: f({id: JUR_ZONING + '_holder'}) },
        { key: JUR_AGENCY, component: Card.Input },
        { key: JUR_CERTIFICATE, component: Card.Select, option: [], placeholder: f({id: JUR_CERTIFICATE + '_holder'}) },
    ]

    return (
        listItems.map(({ component, key, ...rest }) => (
            component({
                key,
                label: f({ id: key }),
                className: style.formRow,
                name: key,
                control,
                ...rest
            })
        ))
    )



}

export default JuridicalInfo