import { Avatar, Button, Col, Modal, notification, Row } from "antd"
import { useFormContext } from "react-hook-form"
import { ReactComponent as IconFlag } from '@assets/icons/IconFlagSolid.svg'
import { ReactComponent as Cancel } from '@assets/icons/Cancel.svg'
import { ReactComponent as Save } from '@assets/icons/Save.svg'
import { ReactComponent as CheckCircle } from '@assets/icons/CheckCircle.svg'
import { ReactComponent as Submit } from '@assets/icons/Submit.svg'
import { FORM_EDIT } from '@components/hook-form/validation/formEdit'
import { useIntl } from "react-intl"
import Card from './layout'
import style from './style.module.css'
import { useHistory } from "react-router-dom"
import { useState } from "react"
import RealEstateServices from "@services/post"

const { ID, AVATAR, POINT_COLLECTION, POINT_EVALUATION, PRICE_SELL, PRICE_SELL_SQUARE, TYPE_POST } = FORM_EDIT

const ROUTE_SELECTED = "/dashboard/PagesSidebar/Selectedland"
const CANCEL = 'cancel'
const SAVE = 'save'
const SUBMIT = 'submit'

const EditHeader = () => {
    const history = useHistory()

    const {handleSubmit} = useFormContext()

    const [openModal, setOpenModal] = useState(false)

    const { control, getValues } = useFormContext()

    const { formatMessage: f } = useIntl()

    const layout = {
        left: { xs: 4, md: 4 },
        middle: { xs: 16, md: 16 },
        right: { xs: 4, md: 4 }
    }

    const viewShape = [
        { name: POINT_COLLECTION, label: f({ id: POINT_COLLECTION }), component: Card.Input },
        { name: POINT_EVALUATION, label: f({ id: POINT_EVALUATION }), component: Card.Input },
        { name: PRICE_SELL, label: f({ id: PRICE_SELL }), component: Card.Input },
        { name: PRICE_SELL_SQUARE, label: f({ id: PRICE_SELL_SQUARE }), component: Card.Input },
        { name: TYPE_POST, label: f({ id: TYPE_POST }), component: Card.Select, option: [{ label: 'demo', value: 1 }, { label: 'test', value: 2 }] },
    ]

    const action = (key) => {
        if(key === CANCEL) history.push(ROUTE_SELECTED)
        if(key === SAVE) {}
        if(key === SUBMIT){
            setOpenModal(true)
        }
    }
    const onSubmit = async(values) => {
        values.id = values.realEstateRawId
        const fake = {id : values.realEstateRawId}
        if(await RealEstateServices.updateRes(fake)){
            notification.success({message: f({id: 'updateSuccess'})})
            history.push(ROUTE_SELECTED)
        }else{
            notification.error({message: f({id: 'updateFail'})})
        }
}

    const _footer = (
        <div style={{gap:10}} className="d-flex">
            <Button onClick={() => setOpenModal(false)} type="primary" size="large" className={`${style.actionConfirm} flex-grow-1`}>
                {f({id: 'cancel'})}
            </Button>
            <Button onClick={handleSubmit(onSubmit)} type="primary" size="large" className={`${style.actionConfirm} flex-grow-1`}>
                {f({id: 'submit'})}
            </Button>
        </div>
    )

    return (
        <Row className={style.editHeader}>
            <Modal wrapClassName={style.confirmModal} bodyStyle={{textAlign: 'center'}} footer={null} closable={false} destroyOnClose visible={openModal} onCancel={() => setOpenModal(false)}>
                <span style={{color:' #60D28D'}}><CheckCircle/></span> 
                <h2 className="mt-1">Xác nhận dữ liệu về bất động sản này đã được  làm sạch?</h2>
                <p className="text-justify text-secondary">Lưu ý: Sau khi nhấn vào nút <b>“Xác nhận”</b>, bạn sẽ không thể thao tác trên bất động sản này nữa.</p>
                {_footer}
            </Modal>
            <Col className="d-flex flex-column justify-content-center align-items-start" {...layout.left}>
                <h2 className={style.titleEdit}>BDS #{getValues(ID)}</h2>
                <div style={{ gap: '5px' }} className="d-flex w-100 align-items-center justify-content-between">
                    <Avatar src={getValues(AVATAR)} />
                    <IconFlag />
                    <span style={{ borderRadius: '5px', fontSize: '12px', fontWeight: 600 }} className="bg-danger flex-grow-1 text-center text-light">Progress</span>
                </div>
            </Col>
            <Col {...layout.middle}>
                <div className="row h-100 align-items-center">
                    {viewShape.map(({ name, label, component, option }) => component({ name, control, label, key: name, option, className: `col text-center ${style.inputBorderBottom}` }))}
                </div>
            </Col>
            <Col className="d-flex align-items-center justify-content-between" {...layout.right}>
                <Button onClick={() => action(CANCEL)} className={style.btnCancel} shape="circle" icon={<Cancel />} />
                <Button onClick={() => action(SAVE)} className={style.btnSave} shape="circle" icon={<Save />} />
                <Button onClick={() => action(SUBMIT)} className={style.btnSubmit} shape="circle" icon={<Submit />} />
            </Col>
        </Row>

    )
}

export default EditHeader