import { Button } from "antd"
import style from './style.module.css'
import { ReactComponent as FileDocument } from '@assets/icons/FileDocument.svg'
import { useState } from "react"

const NoteStick = ({ content }) => {
    const [open, setOpen] = useState(false)

    return (
        <div className="fixed-bottom">
            <div className="position-relative bg-danger">
                <div className={style.noteStick} style={{ bottom: open ? 0 : -170 }}>
                    <Button onClick={() => setOpen(e => !e)} icon={<FileDocument />} shape="circle" size="large" />
                    <div className={`m-1 pr-1 ${style.noteContent}`}>
                        {content || <p className="text-mute h4 text-center mt-2">Chưa có ghi chú nào</p>}
                    </div>
                </div>

            </div>
        </div>
    )
}

export default NoteStick