import InputForm from '@components/hook-form/components/InputForm'
import SelectForm from '@components/hook-form/components/SelectForm'
import CheckBoxForm from '@components/hook-form/components/CheckBoxForm'
import style from './style.module.css'

const Card = ({ children, shadow, className, color }) => {
    const styles = {
        backgroundColor: color,
        ...(!shadow && { boxShadow: 'none' })

    }
    return <div className={`${style.card} ${className} py-1 px-2`} style={styles}>
        {children}
    </div>
}

const CardForm = ({ title, extend, children, classNameBody }) => {
    return <>
        <div className='d-flex justify-content-between '>
            <h2 className={`w-75 ${style.titleForm} text-uppercase`}>{title}</h2>
            <div>{extend}</div>
        </div>
        <div className={classNameBody}>
            {children}
        </div>
    </>
}

const ForField = ({ children, label, className }) => {
    return <div className={className}>
        {label && <h3 className={style.truncate}>{label}</h3>}
        {children}
    </div>
}

const CardInput = ({ control, name, label, className, ...props }) =>
    <ForField label={label} className={className}>
        <InputForm control={control} name={name} {...props} />
    </ForField>

const CardSelect = ({ name, control, option, label, className, ...props }) =>
    <ForField label={label} className={className}>
        <SelectForm className={style.selector} control={control} name={name} option={option} {...props} />
    </ForField>

const CardCheckBox = ({ name, control, option, ...props }) => <CheckBoxForm className={style.checkBox} name={name} control={control} option={option} {...props} />

Card.Form = CardForm
Card.Input = CardInput
Card.Select = CardSelect
Card.Checkbox = CardCheckBox
export default Card